@import url(https://fonts.googleapis.com/css2?family=Comforter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}

.App{
  background-color: #111;
}
.row {
	margin-left: 20px;
	color: white
}

.row_posters {
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	
}

.row_posters::-webkit-scrollbar {
	display: none;
}
.row_poster {
	object-fit: contain;
	width: 100%;
	max-height: 150px;
	margin-right: 10px;
	transition: -webkit-transform 450ms;
	transition: transform 450ms;
	transition: transform 450ms, -webkit-transform 450ms;
}
.row_poster:hover {
	-webkit-transform: scale(1.08);
	        transform: scale(1.08);
}

.row_posterLarge{
	max-height: 250px;
}

.row_posterLarge:hover{
	-webkit-transform: scale(1.09);
	        transform: scale(1.09);
	opacity: 1;
}
.banner {
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
 
.banner_title{
    font-size: 3rem; 
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgb(51, 51, 51, 0.5);
    
}

.banner_button:hover{
    color: #000;
    background-color: #ebe6e6; 
    transition: all 0.2s;
}
.banner_fadeout{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, 
        transparent, 
        rgb(37,37,37,0.61),
        #111);
}

.nav {
    display: flex;
    position: fixed; 
    width: 100%;
    height: 60px;
    top: 0px;
    justify-content: space-between;
    z-index: 1;
    padding-top: 20px;
    transition-timing-function: ease-in-out;
    transition: all 0.6s;
}

.nav_black{
    background-color: #111;
}

.nav_logo{
    padding-left: 20px;
    object-fit: contain;
    padding-bottom: 30px;
    
}

.nav_avatar{
    margin-right: 20px;
    object-fit: contain;
    border-radius: 20%;
    padding-bottom: 5px;
}


.programmer{
    color:#d11717;
    font-family: 'Comforter', cursive;
    font-size: 40px;
    visibility: hidden;
}

.programmer_hide{
    visibility: visible;
    transition-timing-function: ease-in-out;
    transition: all 0.5s;
}
